@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
}

.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}
.btn-edit {
  display: inline-block;
  background:  rgb(58, 0, 112);
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}




.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
  border: 1px solid rgb(72, 166, 255);
  border-radius: 5px;

  display: grid;
  grid-template-columns: 7fr 1fr;
}

.task.reminder {
  border-left: 5px solid green;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* ADDED BY ME */

.buttons{
  display: grid;
  align-items: center;
}

.no-task{
  color: red;
  text-align: center;
  margin-top: 50px;
}

.moment{
  font-size: smaller;
  margin-top: 20px;
}

.delete-btn {
  width: 50px;
  height: 30px;
  background-color: rgb(226, 19, 19);
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  border: 1px solid aquamarine;
  border-radius: 5px;
  cursor: pointer;
  
}

.edit-btn{
  width: 50px;
  height: 30px;
  background-color: rgb(58, 0, 112);
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  border: 1px solid aquamarine;
  border-radius: 5px;
  cursor: pointer;
}




.add-form {
  margin-bottom: 40px;

}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}


.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1;
}

.form-control-check input {
  flex: 2;
  height: 20px;
}


footer {
  margin-top: 30px;
  text-align: center;
}